	module.exports = {"data":{"markdownRemark":{"html":"<p>Actually this is better than Thursday morning TV, which, in my hometown at least, was pretty weak. This stuff is amazing. </p>\n<ol>\n\t<li>First some of the best nonprofit advertising I've ever seen.</li>\n \t<li>Second a great bike water filter pump. \t</li>\n  <li>Lastly a favorite app redesigned.</li>\n</ol>\n<p>[http://www.youtube.com/v/WIvmE4_KMNw&#x26;hl=en]</p>\n<p>(more <a href=\"http://www.ideaography.net/more-video-type-love/\">type video</a> if you're into it) </p>\n<p>And the bike:</p>\n<p>[http://www.youtube.com/v/-U-mvfjyiao&#x26;hl=en]]</p>\n<p>The wacky bike was designed by IDEO people — I'm also impressed with their recent riff on the magazine quiz (maybe think madlibs): A Rockefeller sponsored <a href=\"https://client.ideo.com/socialimpact/\">guide to creating social impact</a> with your design firm. </p>\n<p>And the App: </p>\n<p><a href=\"http://www.frontlinesms.com/\">FrontlineSMS</a> is a thoroughly wonderful idea in many ways ... I mean, if you're into international rural research with mobile phones. A tool worth watching very closely, it's what I think is the leading platform of the mobile research \"industry\". (if there is such a thing.)</p>\n<p>They just released a major new milestone and have bunch of great new branding. Great work, <a href=\"http://www.blogspot.kiwanja.net/\">Ken</a>!</p>\n<img src=\"/static/images/frontline.jpg\" alt=\"frontline interface\"/>","frontmatter":{"created_at":"June 26, 2008","path":"/2008/06/26/thursday-morning-tv","title":"Thursday Morning TV","tags":["video","design","ux"]}}},"pathContext":{}}

